  font-family: 'Trade Gothic LT Bold Italic';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
@font-face {
  font-family: 'Helvetica Neue LT Std Medium';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}
@font-face {
  font-family: 'Helvetica Neue LT Std Bold';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

* {
  font-family: 'Helvetica Neue LT Std Medium', sans-serif;
}

.hamburger-icon {
  display: none !important;
  color: #fff;
  font-size: 26px;
}
div#hamburger-overlay {
  background-color: #000000a3;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  display: none;
  padding-bottom: 35px;
}

.hamburger-exit {
  font-size: 32px;
  color: #fff;
  position: absolute;
  right: 25px;
  top: 25px;
}

.hamburger-icon:hover,
.hamburger-exit:hover {
  cursor: pointer;
  color: #ed8b00;
}

#mobile-menu li a {
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 0.35px;
  color: #fff;
  height: 52px;
  padding: 10px 14px 10px 15px;
  line-height: 45px;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.2s ease;
  font-family: 'Trade Gothic LT Bold Italic';
}

ul#mobile-menu {
  padding: 50px;
  background-color: black;
}

@media only screen and (max-width: 1000px) {
  #navWrapper #menu {
    display: none;
  }
  .hamburger-icon {
    display: block !important;
  }

  .tableMain_wrap {
    overflow-x: scroll;
  }
}
/* Dropdown Styling */
.fa-caret-left,
.fa-caret-down {
  position: absolute;
  right: 25px;
  transform: scale(1.5);
}
.fa-solid:hover {
  color: #ed8b00;
  transition: all 0.1s ease;
}
.dropdown-btn {
  display: flex;
}
.dropdown-container {
  display: none;
}
:not(.dropdown-btn.active) .fa-caret-down {
  display: none;
}
.dropdown-btn.active .fa-caret-left {
  display: none;
}
.dropdown-btn.active .fa-caret-down {
  display: block;
}

/* Subcategory Cell Styling */
.prodCell.parentSubcategoryCell {
  border-color: #eee;
  border-width: 3px;
  padding: 0px !important;
}

.subcategoryName {
  background-color: #000000a8;
  color: #eee;
  top: 0px;
  right: 0px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
}

.prodCell.parentSubcategoryCell:hover .subcategoryName {
  display: none;
}

.parentSubcategoryCell:hover .browseBtn {
  background: #000000a8;
}

#menu li a {
  font-family: 'Trade Gothic LT Bold Italic', sans-serif;
}

/* Hide phone number and email of sales rep/CSR on My Account page */
.repInfo table,
.contactSalesInfo table {
  display: none;
}

/* Hide Change Login ID, Change Password, and My Profile links on My Account page */
#QLloginID,
#QLpassword,
#QLprofile {
  display: none;
}

table.dataTable thead th div.DataTables_sort_wrapper span {
  right: unset;
  left: 0px;
  background-color: #e2e2e2;
  border-radius: 4px;
}

#datatable th.ui-state-default {
  border-radius: 0px;
}

.dt-buttons button.ui-state-default {
  background-color: var(--primary-color);
  color: var(--primary-color-text);
}

#mobile-menu li a:hover {
  color: var(--navbar-hover) !important;
}

/* Hides Delivery Time and Est. Cost columns in the shipping method table */
table#shipping_method_table tbody tr td:nth-child(5),
#shipMethodBox header table tbody tr td:nth-child(4),
table#shipping_method_table tbody tr td:nth-child(3),
#shipMethodBox header table tbody tr td:nth-child(2) {
  display: none;
}

/* Hides Turnaround Fee and Shipping & Handling Charges in Order Summary */
div#taxRushShipGrand tr:nth-of-type(3),
div#taxRushShipGrand tr:nth-of-type(5) {
  display: none;
}

/* Hides PO field on payment page */
div#custPOBox {
  display: none;
}

div#orderPaymentType {
  display: none;
}
